<template>
  <div class="cardContainer">
    <div class="userCards" v-if="userdata">
      <div class="userCard" v-for="user in userdata" v-bind:key="user.id">
        <div class="profile">
          <img class="profilePic" :src="user.picture.large" alt="profilepic" />
          <div class="profileText">
            <h4 class="cardName">{{ user.name.first }} {{ user.name.last }}</h4>

            <p class="profileInfo">Email : {{ user.email }}</p>
            <p class="profileInfo">Cellphone : {{ user.cell }}</p>
            <p class="profileInfo">
              From: {{ user.location.city }}, {{ user.location.country }}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Usercard",
  data() {
    return {
      userdata: null,
    };
  },

  methods: {
    getUsers(amount) {
      fetch(`https://randomuser.me/api/?results=${amount}`)
        .then((res) => res.json())
        .then((data) => (this.userdata = data.results))
        .catch((err) => console.log(err));
    },
  },
};
</script>

