<template>
  <h1>Random User Generator</h1>
  <div class="generate">
    <p class="generateTxt">Generate</p>
    <input type="number" v-model="inputNumber" />

    <p class="generateTxt">users</p>
  </div>
  <div class="btnanderr">
    <p class="errTxt">{{ noInputErr }}</p>
    <p class="errTxt">{{ tooManyErr }}</p>
    <button class="button-68" @click="updateCount()">Go!</button>
  </div>
  <Usercard ref="usercard"></Usercard>
</template>

<script>
import Usercard from "./components/Usercard.vue";

export default {
  name: "App",
  components: {
    Usercard,
  },
  data() {
    return {
      noInputErr: "",
      tooManyErr: "",
      inputNumber: null,
      userCount: null,
    };
  },
  methods: {
    updateCount() {
      if (!this.inputNumber) {
        this.tooManyErr = "";
        this.noInputErr = "Please enter a number";
      } else if (this.inputNumber > 100) {
        this.noInputErr = "";
        this.tooManyErr = "Calm down, let's keep it under 100";
      } else {
        this.userCount = this.inputNumber;
        this.$refs.usercard.getUsers(this.userCount);
        this.noInputErr = "";
        this.tooManyErr = "";
      }
    },
  },
};
</script>

